
'use client';

import { useEffect, useState } from 'react';
import { useUser } from '@/hooks/use-user';
import { collection, query, where, onSnapshot, getCountFromServer } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import type { Permit, UserRole } from '@/types';
import { Card, CardHeader, CardTitle, CardContent, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { PlusCircle, FileText, Loader2, BarChart, Users, AlertCircle } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { DataTable } from '@/app/permits/data-table';
import { columns } from '@/app/permits/columns';
import { Bar, BarChart as RechartsBarChart, ResponsiveContainer, XAxis, YAxis, Tooltip } from 'recharts';

type PermitStats = {
  total: number;
  pendiente_revision: number;
  aprobado: number;
  en_ejecucion: number;
  cerrado: number;
};

const getGreeting = () => {
  const hour = new Date().getHours();
  if (hour < 12) return 'Buenos días';
  if (hour < 18) return 'Buenas tardes';
  return 'Buenas noches';
};

export default function DashboardPage() {
  const { user } = useUser();
  const router = useRouter();
  const [stats, setStats] = useState<PermitStats | null>(null);
  const [recentPermits, setRecentPermits] = useState<Permit[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!user) return;

    const canViewAll = user.role === 'admin' || user.role === 'autorizante' || user.role === 'lider_sst';
    
    // --- Consulta para Estadísticas ---
    const fetchStats = async () => {
      const statuses: PermitStats = { total: 0, pendiente_revision: 0, aprobado: 0, en_ejecucion: 0, cerrado: 0 };
      const statusKeys = Object.keys(statuses) as (keyof PermitStats)[];

      const statPromises = statusKeys.map(async (status) => {
        let q;
        const baseQuery = collection(db, 'permits');
        
        if (canViewAll) {
            q = status === 'total' ? baseQuery : query(baseQuery, where('status', '==', status));
        } else {
            q = status === 'total' 
                ? query(baseQuery, where('createdBy', '==', user.uid))
                : query(baseQuery, where('createdBy', '==', user.uid), where('status', '==', status));
        }
        const snapshot = await getCountFromServer(q);
        return { status, count: snapshot.data().count };
      });

      const results = await Promise.all(statPromises);
      const newStats = results.reduce((acc, { status, count }) => ({ ...acc, [status]: count }), {} as PermitStats);
      setStats(newStats);
    };

    // --- Consulta para Permisos Recientes ---
    let recentPermitsQuery;
    if (canViewAll) {
      recentPermitsQuery = query(collection(db, 'permits'));
    } else {
      recentPermitsQuery = query(collection(db, 'permits'), where('createdBy', '==', user.uid));
    }
    
    const unsubscribe = onSnapshot(recentPermitsQuery, snapshot => {
      const permitsData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Permit));
      setRecentPermits(permitsData);
      setLoading(false);
    }, error => {
      console.error("Error fetching recent permits:", error);
      setLoading(false);
    });

    fetchStats();

    return () => unsubscribe();
  }, [user]);

  const chartData = stats ? [
    { name: 'Pendientes', value: stats.pendiente_revision, fill: 'hsl(var(--warning))' },
    { name: 'Aprobados', value: stats.aprobado, fill: 'hsl(var(--success))' },
    { name: 'En Ejecución', value: stats.en_ejecucion, fill: 'hsl(var(--info))' },
    { name: 'Cerrados', value: stats.cerrado, fill: 'hsl(var(--primary))' },
  ] : [];

  if (loading) {
    return <div className="flex h-full w-full items-center justify-center"><Loader2 className="h-8 w-8 animate-spin" /></div>;
  }

  return (
    <div className="flex flex-1 flex-col gap-4 p-4 md:gap-8 md:p-8">
      <div className="flex flex-col md:flex-row items-start md:items-center justify-between gap-4">
        <div>
          <h1 className="text-2xl font-bold tracking-tight">{getGreeting()}, {user?.displayName || 'Usuario'}!</h1>
          <p className="text-muted-foreground">Aquí tienes un resumen de la actividad reciente.</p>
        </div>
        <div className="flex items-center gap-2">
           <Button onClick={() => router.push('/permits/create?new=true')} className="bg-nixus hover:bg-nixus/90">
            <PlusCircle className="mr-2 h-4 w-4" /> Nuevo Permiso
          </Button>
          <Button variant="outline" onClick={() => router.push('/permits')}>
            <FileText className="mr-2 h-4 w-4" /> Ver Todos
          </Button>
        </div>
      </div>

      {/* --- Cards de Estadísticas --- */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Permisos Totales</CardTitle>
            <Users className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats?.total ?? <Loader2 className="h-6 w-6 animate-spin" />}</div>
            <p className="text-xs text-muted-foreground">Total de permisos creados</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Pendientes de Revisión</CardTitle>
            <AlertCircle className="h-4 w-4 text-yellow-500" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats?.pendiente_revision ?? <Loader2 className="h-6 w-6 animate-spin" />}</div>
             <p className="text-xs text-muted-foreground">Esperando aprobación</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">En Ejecución</CardTitle>
            <Loader2 className="h-4 w-4 text-purple-500 animate-spin" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats?.en_ejecucion ?? <Loader2 className="h-6 w-6 animate-spin" />}</div>
            <p className="text-xs text-muted-foreground">Trabajos activos actualmente</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Cerrados</CardTitle>
            <BarChart className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats?.cerrado ?? <Loader2 className="h-6 w-6 animate-spin" />}</div>
             <p className="text-xs text-muted-foreground">Permisos completados</p>
          </CardContent>
        </Card>
      </div>
      
       {/* --- Gráfica y Tabla --- */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
        <Card>
            <CardHeader>
                <CardTitle>Distribución de Permisos</CardTitle>
                <CardDescription>Resumen visual del estado de los permisos.</CardDescription>
            </CardHeader>
            <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                    <RechartsBarChart data={chartData}>
                        <XAxis dataKey="name" stroke="#888888" fontSize={12} tickLine={false} axisLine={false} />
                        <YAxis stroke="#888888" fontSize={12} tickLine={false} axisLine={false} />
                        <Tooltip cursor={{ fill: 'hsl(var(--muted))' }} />
                        <Bar dataKey="value" radius={[4, 4, 0, 0]} />
                    </RechartsBarChart>
                </ResponsiveContainer>
            </CardContent>
        </Card>
        <Card>
            <CardHeader>
                <CardTitle>Permisos Recientes</CardTitle>
                <CardDescription>
                    Los últimos permisos gestionados en el sistema.
                </CardDescription>
            </CardHeader>
            <CardContent>
               <DataTable columns={columns} data={recentPermits.slice(0, 5)} />
            </CardContent>
        </Card>
      </div>
    </div>
  );
}
