
'use client';

import type { ColumnDef } from '@tanstack/react-table';
import type { Permit, PermitStatus } from '@/types';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { ArrowUpDown, Eye } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { format } from 'date-fns';
import { es } from 'date-fns/locale';

const statusConfig: Record<PermitStatus, { text: string; className: string }> = {
  borrador: { text: 'Borrador', className: 'bg-gray-100 text-gray-800' },
  pendiente_revision: { text: 'Pendiente', className: 'bg-yellow-100 text-yellow-800' },
  aprobado: { text: 'Aprobado', className: 'bg-green-100 text-green-800' },
  en_ejecucion: { text: 'En Ejecución', className: 'bg-purple-100 text-purple-800' },
  suspendido: { text: 'Suspendido', className: 'bg-orange-100 text-orange-800' },
  cerrado: { text: 'Cerrado', className: 'bg-blue-100 text-blue-800' },
  rechazado: { text: 'Rechazado', className: 'bg-red-100 text-red-800' },
};

const DataTableActionCell = ({ permitId }: { permitId: string }) => {
    const router = useRouter();
    return (
        <Button variant="ghost" size="icon" onClick={() => router.push(`/permits/${permitId}`)}>
            <Eye className="h-4 w-4" />
        </Button>
    )
}

const formatDateSafe = (date: any) => {
    if (!date) return 'N/A';
    try {
        const dateObj = date.toDate ? date.toDate() : new Date(date);
        if (isNaN(dateObj.getTime())) return 'Fecha inválida';
        return format(dateObj, 'dd/MM/yyyy HH:mm', { locale: es });
    } catch {
        return 'Fecha inválida';
    }
}


export const columns: ColumnDef<Permit>[] = [
  {
    accessorKey: 'number',
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === 'asc')}
        >
          Número
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      );
    },
    cell: ({ row }) => <div className="font-medium">{row.original.number || 'N/A'}</div>
  },
  {
    accessorKey: 'status',
    header: 'Estado',
    cell: ({ row }) => {
      const status = row.getValue('status') as PermitStatus;
      const config = statusConfig[status] || statusConfig.borrador;
      return <Badge className={config.className}>{config.text}</Badge>;
    },
  },
  {
    accessorKey: 'generalInfo.workDescription',
    header: 'Descripción',
    cell: ({ row }) => <div className="truncate max-w-xs">{row.original.generalInfo?.workDescription || 'N/A'}</div>,
  },
  {
    accessorKey: 'user.displayName',
    header: 'Solicitante',
    cell: ({ row }) => <div>{row.original.user?.displayName || 'N/A'}</div>,
  },
  {
    accessorKey: 'createdAt',
    header: ({ column }) => {
        return (
          <Button
            variant="ghost"
            onClick={() => column.toggleSorting(column.getIsSorted() === 'asc')}
          >
            Fecha de Creación
            <ArrowUpDown className="ml-2 h-4 w-4" />
          </Button>
        );
      },
    cell: ({ row }) => <div>{formatDateSafe(row.original.createdAt)}</div>
  },
  {
    id: 'actions',
    cell: ({ row }) => <DataTableActionCell permitId={row.original.id} />,
  },
];
